import React, { useState } from 'react';
import { auth } from '../firebase';
import { Spinner } from 'react-bootstrap';
import './Login.scss';

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleLogin = (e) => {
    e.preventDefault();
    setLoading(true);
    auth
      .signInWithEmailAndPassword(email, password)
      .then((authUser) => {
        if (authUser) {
          setEmail('');
          setPassword('');
          setLoading(false);
        }
      })
      .catch((error) => {
        setError(error.message);
        setLoading(false);
      });
  };
  return (
    <div className='login d-flex justify-content-center flex-column align-items-center'>
      <h1>Login</h1>

      <form onSubmit={handleLogin} className='login__form'>
        {error && <p className=' text-danger p-1'>{error}</p>}
        <input
          placeholder='Email...'
          className='form-control mt-5'
          type='email'
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          autoComplete='current-email'
        />
        <input
          placeholder='Password..'
          className='form-control mt-3'
          type='password'
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          autoComplete='current-password'
        />
        <button className='btn btn-primary form-control mt-5' disabled={(!email && !password) || loading}>
          <span className='mr-2'>SUBMIT</span>
          <span>{loading && <Spinner animation='border' size='sm' />}</span>
        </button>
      </form>
    </div>
  );
};

export default Login;
