import React from 'react';

import { Badge } from 'react-bootstrap';

import { Trash, Pencil, Link45deg } from 'react-bootstrap-icons';
import { ReactComponent as GithubLogo } from '../logos/github-logo.svg';

const Project = ({ project, handleRemove, handleEdit }) => {
  return (
    <div className='project  grid-item'>
      <div className='ribbon-wrapper'>
        <div className={`ribbon-tag ${project.isPublic ? 'public' : ''}`}>
          {project.isPublic ? 'Public' : 'Private'}
        </div>
      </div>
      <div className='project-image mb-2'>
        <img src={project.workImageURL} alt={project.title} />
      </div>
      <div>
        <h3>{project.title}</h3>
        <p>{project.description}</p>
        <div>
          {project.technologies.map((item, index) => (
            <Badge key={index} variant='danger' size='lg' className='mr-2'>
              {item}
            </Badge>
          ))}
        </div>
        <br />

        <div className='actions-box'>
          <a
            className=' actions-item action-edit-project'
            href={project.siteURL}
            target='_blank'
            rel='noopener noreferrer'
          >
            <Link45deg size={30} />
          </a>
          <a
            className='actions-item action-remove-project'
            href={project.githubURL}
            target='_blank'
            rel='noopener noreferrer'
          >
            <GithubLogo className='github-logo' width={30} height={30} />
          </a>
          <div className=' actions-item action-edit-project' onClick={() => handleEdit(project)}>
            <Pencil size={30} />
          </div>
          <div className='actions-item action-remove-project' onClick={() => handleRemove(project)}>
            <Trash size={25} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Project;
