import React, { useState, useEffect, useRef } from 'react';
import { db } from '../firebase';
import { Spinner, Modal, Button, InputGroup, Form, FormControl, Badge, Alert } from 'react-bootstrap';
import { generateSlug, uploadImageAsPromise, getFileRef, getBase64 } from '../helper/helper';
// import useStorage from '../hooks/useStorage';

import './AddProject.scss';

import { Image } from 'react-bootstrap-icons';

const EditProjectModal = ({ show, onHide, setAlert, editingProject, setEditingProject }) => {
  const newProjectForm = useRef();
  const [title, setTitle] = useState('');
  const [siteURL, setSiteURL] = useState('');
  const [githubURL, setGithubURL] = useState('');
  const [description, setDescription] = useState('');

  const [isPublic, setIsPublic] = useState(true);
  const [showOnHomePage, setShowOnHomePage] = useState(null);

  const [homeImage, setHomeImage] = useState(null);
  const [homeImageURL, setHomeImageURL] = useState('');
  const [homeImagePreviewURL, setHomeImagePreviewURL] = useState('');

  const [workImageURL, setWorkImageURL] = useState('');
  const [workImage, setWorkImage] = useState(null);
  const [workImagePreviewURL, setWorkImagePreviewURL] = useState('');

  const [technology, setTechnology] = useState('');
  const [technologies, setTechnologies] = useState([]);
  const [order, setOrder] = useState('');
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    let isSubscribe = true;
    if (isSubscribe) {
      setTitle(editingProject.title);
      setSiteURL(editingProject.siteURL);
      setGithubURL(editingProject.githubURL);
      setDescription(editingProject.description);
      setIsPublic(editingProject.isPublic);
      setShowOnHomePage(editingProject.showOnHomePage);
      setTechnologies(editingProject.technologies);
      setHomeImageURL(editingProject.homeImageURL);
      setWorkImageURL(editingProject.workImageURL);
      setOrder(editingProject.order)
    }

    return () => {
      console.log('unmounted');
      isSubscribe = false;
    };
  }, [editingProject]);

  const handleAddTechnology = () => {
    if (technology.trim()) {
      setTechnologies((previousState) => {
        return [...previousState, technology];
      });
      setTechnology('');
    }
  };

  const handleRemoveTechnology = (index) => {
    // console.log(index);
    let newArray = [...technologies];
    newArray.splice(index, 1);
    setTechnologies(newArray);
  };

  const onHomeImageChange = (e) => {
    const selected = e.target.files[0];
    setHomeImage(selected);
    getBase64(selected, (result) => {
      setHomeImagePreviewURL(result);
    });
  };

  const onWorkImageChange = (e) => {
    const selected = e.target.files[0];
    setWorkImage(selected);
    getBase64(selected, (result) => {
      setWorkImagePreviewURL(result);
    });
  };

  const handleSubmit = async () => {
    if (validateForm()) {
      setLoading(true);

      try {
        let updateProject = {
          title,
          slug: generateSlug(title),
          technologies,
          showOnHomePage,
          isPublic,
          siteURL,
          githubURL,
          description,
          order: parseInt(order)
        };
        // if user choose to update homeImage
        if (homeImage) {
          const newHomeImageURL = await uploadImageAsPromise(homeImage);
          await getFileRef(homeImageURL).delete();
          updateProject = {
            ...updateProject,
            homeImageURL: newHomeImageURL,
          };
        }
        // if user choose to update workImage
        if (workImage) {
          const newWorkImageURL = await uploadImageAsPromise(workImage);
          await getFileRef(workImageURL).delete();
          updateProject = {
            ...updateProject,
            workImageURL: newWorkImageURL,
          };
        }

        db.collection('projects')
          .doc(editingProject.id)
          .update(updateProject)
          .then(async () => {
            console.log('finish update');
            handleCloseForm();
            setAlert({
              type: 'success',
              message: 'Project has been updated!',
            });
            onHide();
          });
      } catch (error) {
        handleCloseForm();

        // set alert
        setAlert({
          type: 'error',
          message: error.message,
        });
      }
    } else {
      setError('Please fill out all required fields.');
      setTimeout(() => {
        setError('');
      }, 3000);
    }
  };

  // update to database

  // handle close form

  const handleCloseForm = () => {
    if (!loading) {
      onHide();
      resetForm();
      setLoading(false);
      setEditingProject(null);
    }
  };

  const validateForm = () => {
    if (!title || !siteURL || !description) {
      return false;
    } else return true;
  };

  // reset form function
  const resetForm = () => {
    setTitle('');
    setDescription('');
    setSiteURL('');
    setGithubURL('');
    setTechnologies([]);
    setHomeImage(null);
    setWorkImage(null);
    setOrder('');
  };

  return (
    <Modal
      animation={false}
      show={show}
      onHide={handleCloseForm}
      size='lg'
      aria-labelledby='contained-modal-title-vcenter'
      scrollable
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id='contained-modal-title-vcenter'>Edit Project</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {error && <Alert variant='danger'>{error}</Alert>}
        <Form className='new-project-form' ref={newProjectForm} onSubmit={handleSubmit}>
          <Form.Group>
            <Form.Label htmlFor='title' className='required font-weight-bold'>
              Title:
            </Form.Label>
            <Form.Control required type='text' id='title' value={title} onChange={(e) => setTitle(e.target.value)} />
          </Form.Group>

          <Form.Group>
            <Form.Label htmlFor='siteURL' className='required font-weight-bold'>
              Site URL:
            </Form.Label>
            <Form.Control
              required
              type='text'
              id='siteURL'
              value={siteURL}
              onChange={(e) => setSiteURL(e.target.value)}
            />
          </Form.Group>

          <Form.Group>
            <Form.Label htmlFor='githubURL' className='font-weight-bold'>
              Github URL:
            </Form.Label>
            <Form.Control type='text' id='githubURL' value={githubURL} onChange={(e) => setGithubURL(e.target.value)} />
          </Form.Group>

          <Form.Group>
            <Form.Label className='required font-weight-bold'>Description:</Form.Label>
            <Form.Control
              as='textarea'
              required
              rows='3'
              value={description}
              onChange={(e) => setDescription(e.target.value)}
            />
          </Form.Group>

          <Form.Group>
            <Form.Label className='required font-weight-bold'>Image on homepage:</Form.Label>
            <Form.File onChange={onHomeImageChange} id='input-homeImage' accept='image/*' style={{ display: 'none' }} />
            <div>
              <label htmlFor='input-homeImage' className='btn btn-outline-primary'>
                <div className='d-flex align-items-center'>
                  <Image className='mr-2' />
                  <span> Change image</span>
                </div>
              </label>
              {homeImage && <span className='ml-3'>{homeImage.name}</span>}
            </div>
            {homeImageURL && !homeImagePreviewURL && (
              <img className='project-image-preview' src={homeImageURL} alt={editingProject.title} />
            )}
            {homeImagePreviewURL && (
              <img className='project-image-preview' src={homeImagePreviewURL} alt={editingProject.title} />
            )}
          </Form.Group>

          <Form.Group>
            <Form.Label className='required font-weight-bold'>Image on portfolio page:</Form.Label>
            <Form.File onChange={onWorkImageChange} id='input-workImage' accept='image/*' style={{ display: 'none' }} />
            <div>
              <label htmlFor='input-workImage' className='btn btn-outline-primary'>
                <div className='d-flex align-items-center'>
                  <Image className='mr-2' />
                  <span> Change image</span>
                </div>
              </label>
              {workImage && <span className='ml-3'>{workImage.name}</span>}
            </div>
            {workImageURL && !workImagePreviewURL && (
              <img className='project-image-preview' src={workImageURL} alt={editingProject.title} />
            )}
            {workImagePreviewURL && (
              <img className='project-image-preview' src={workImagePreviewURL} alt={editingProject.title} />
            )}
          </Form.Group>

          <Form.Group>
            <Form.Label className='font-weight-bold'>Public:</Form.Label>
            <div className='d-flex'>
              <Form.Check
                className='mr-5'
                type='radio'
                label='Yes'
                name='public-status'
                onChange={(e) => setIsPublic(true)}
                checked={isPublic === true}
              />
              <Form.Check
                type='radio'
                label='No'
                name='public-status'
                onChange={(e) => setIsPublic(false)}
                checked={isPublic === false}
              />
            </div>
          </Form.Group>

          <Form.Group>
            <Form.Label className='font-weight-bold'>Show on homepage:</Form.Label>
            <div className='d-flex'>
              <Form.Check
                className='mr-5'
                type='radio'
                label='Yes'
                name='showHomepage'
                onChange={(e) => setShowOnHomePage(true)}
                checked={showOnHomePage === true}
              />
              <Form.Check
                type='radio'
                label='No'
                name='showHomepage'
                onChange={(e) => setShowOnHomePage(false)}
                checked={showOnHomePage === false}
              />
            </div>
          </Form.Group>

          <Form.Group>
            <Form.Label className='font-weight-bold'>Technologies:</Form.Label>
            <InputGroup className='mb-3 w-50'>
              <FormControl
                placeholder='Technology...'
                aria-label='technology'
                value={technology}
                onChange={(e) => setTechnology(e.target.value)}
              />
              <InputGroup.Append>
                <Button variant='outline-primary' disabled={!technology.trim()} onClick={handleAddTechnology}>
                  Add
                </Button>
              </InputGroup.Append>
            </InputGroup>
            <div className='projectForm___technologies-list'>
              {technologies.map((item, index) => (
                <span className='mr-4 mb-5 projectForm___technologies-item' key={index}>
                  <Badge pill variant='danger' className=' px-3 py-2'>
                    {item}
                  </Badge>
                  <span className='btn-remove-technology' onClick={() => handleRemoveTechnology(index)}>
                    &times;
                  </span>
                </span>
              ))}
            </div>
          </Form.Group>
          <Form.Group>
            <Form.Label className='font-weight-bold'>Order</Form.Label>
              <Form.Control type="number" name="order" value={order} onChange={(e) => setOrder(e.target.value)} />
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant='outline-primary' onClick={handleSubmit} disabled={loading}>
          <span className='mr-2'>Save</span>
          <span>{loading && <Spinner animation='border' size='sm' />}</span>
        </Button>
        <Button variant='outline-danger' disabled={loading} onClick={handleCloseForm}>
          Cancel
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default EditProjectModal;
