import React, { useState, useEffect } from 'react';
import NavigationBar from './NavigationBar';
import { Alert } from 'react-bootstrap';
import AddProjectModal from './AddProjectModal';
import EditProjectModal from './EditProjectModal';
import Project from './Project';
import { db } from '../firebase';

import { getFileRef } from '../helper/helper';

const Dashboard = () => {
  const [showAddProjectModal, setShowAddProjectModal] = useState(false);
  const [showEditProjectModal, setShowEditProjectModal] = useState(false);
  const [projects, setProjects] = useState([]);
  const [alert, setAlert] = useState(null);
  const [searchKeyword, setSearchKeyword] = useState('');
  const [results, setResults] = useState([]);
  const [filterBy, setFilterBy] = useState('all');

  const [editingProject, setEditingProject] = useState(null);

  useEffect(() => {
    const unsubscribe = db
      .collection('projects')
      .orderBy('order', 'asc')
      .onSnapshot((snapshot) => {
        const arr = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));

        setProjects(arr);
      });

    return () => unsubscribe();
  }, []);

  const handleRemove = async (project) => {
    let confirmed = window.confirm('Are you sure you want to remove this project?');
    if (confirmed) {
      // delete file from storage first and then delete document
      try {
        await getFileRef(project.homeImageURL).delete();
        await getFileRef(project.workImageURL).delete();

        db.collection('projects')
          .doc(project.id)
          .delete()
          .then(() => {
            setAlert({
              type: 'success',
              message: 'Project has been removed.',
            });
          });
      } catch (error) {
        console.log(error.message);
        setAlert({
          type: 'error',
          message: error.message,
        });
      }
    } else return;
  };

  //handle edit

  const handleEdit = (project) => {
    setShowEditProjectModal(true);
    setEditingProject(project);
  };

  // remove alert after 3 seconds
  useEffect(() => {
    setTimeout(() => {
      setAlert(null);
    }, 3000);
  }, [alert]);

  //handle search and filter

  useEffect(() => {
    let projectsArray = [...projects];

    if (searchKeyword.length > 0) {
      projectsArray = projectsArray.filter((project) =>
        project.title.toLowerCase().includes(searchKeyword.toLowerCase().trim())
      );
    }

    if (filterBy === 'public') {
      projectsArray = projectsArray.filter((project) => project.isPublic === true);
    } else if (filterBy === 'private') {
      projectsArray = projectsArray.filter((project) => project.isPublic === false);
    }

    setResults(projectsArray);
  }, [searchKeyword, projects, filterBy]);

  return (
    <div className='dashboard'>
      <NavigationBar searchKeyword={searchKeyword} setSearchKeyword={setSearchKeyword} />

      <AddProjectModal setAlert={setAlert} show={showAddProjectModal} onHide={() => setShowAddProjectModal(false)} />

      {editingProject && (
        <EditProjectModal
          editingProject={editingProject}
          setEditingProject={setEditingProject}
          setAlert={setAlert}
          show={showEditProjectModal}
          onHide={() => setShowEditProjectModal(false)}
        />
      )}

      {alert && (
        <Alert className='dashboard__alert' variant={alert.type === 'success' ? 'success' : 'danger'}>
          {alert.message}
        </Alert>
      )}

      <div className='dashboard__main'>
        <div className='projects-filter mb-5 mt-2 d-flex justify-content-center'>
          <div
            className={`projects-filter-item  ${filterBy === 'all' ? 'active' : ''}`}
            onClick={() => setFilterBy('all')}
          >
            All <span>{projects.length}</span>
          </div>
          <div
            className={`projects-filter-item  ${filterBy === 'public' ? 'active' : ''}`}
            onClick={() => setFilterBy('public')}
          >
            Public <span>{projects.filter((project) => project.isPublic === true).length}</span>
          </div>
          <div
            className={`projects-filter-item  ${filterBy === 'private' ? 'active' : ''}`}
            onClick={() => setFilterBy('private')}
          >
            Private <span>{projects.filter((project) => project.isPublic === false).length}</span>
          </div>
        </div>

        <div className='dashboard__main-grid'>
          <div onClick={() => setShowAddProjectModal(true)} className='text-center p-5 new-project grid-item'>
            <h1>+</h1>
            <p>New Project</p>
          </div>
          {results.map((project) => (
            <Project
              key={project.id}
              project={project}
              handleRemove={handleRemove}
              handleEdit={handleEdit}
              className='grid-item'
            ></Project>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
